import React, { useEffect } from "react";
import { useState } from "react";
import { DropdownButton, Dropdown, Navbar, Row, Col, Button, Fade, Collapse, Container } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Link, NavLink } from "react-router-dom";
import { getAllCategory } from "../../actions/category.action";
import './style.css';

/**
 * @author
 * @function DropDown
 **/

const DropDown = (props) => {
  const category = useSelector((state) => state.category);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getAllCategory());
  }, []);

  const renderCategories = (categories) => {
    if (categories) {
        return categories.map((category) => {
                return (
                  
                    <Col sm={6} md={4} lg xs={6}>
                      <a href={`/product/${category.slug}`}>
                       {category.name}
                      </a>
                    
                    </Col>
                  

                  
                )
            }
        )
    }
}

  // const renderCategories = (categories) => {
  //   const mycategories = [];
  //   for (let category of categories) {
  //     mycategories.push(
  //       <Link to={`/:${category.slug}`}>
  //         <option value={category.slug}>{category.name}</option>
  //       </Link>
  //     );
  //   }
  //   return mycategories;
  // };
  const [open, setOpen] = useState(false);


  return (
      <Container fluid className="drop__down">
        <div className="text-center" style={{padding:"4px"}}>
          <Button
          className="btn__margin"
        onClick={() => setOpen(!open)}
        aria-controls="example-fade-text"
        aria-expanded={open}
      >
        Shop By Category 
      </Button>
      <Collapse in={open}>
          <Row className="justify-content-md-center">
          {category.categories.length > 0
        ? renderCategories(category.categories)
        : null}
          </Row>
          </Collapse>
          
        </div>   
        </Container>     
  );
};

export default DropDown;
