import React from 'react';
import { Table } from 'react-bootstrap';

/**
* @author
* @function PriceDetails
**/

const PriceDetails = (props) => {
  return(
    <Table size="sm">
  <thead>
    <h5>Payment Details</h5>
  </thead>
  <tbody>
    <tr>
      <td>Price ({props.totalItem} items)</td>
      <td>{props.totalMrp}</td>
    </tr>
    <tr>
      
      
      <td>Discount</td>
      <td>-{props.totalMrp-props.totalPrice}</td>
    </tr>
    <tr>

      <td>Delivery Charges</td>
      {props.totalPrice > 499 ? <td style={{color:"#00a100"}}>FREE</td> : <td>+ 18</td>}
      
    </tr>

    <tr>
      <td><b>Total Payable Amount</b></td>
      {props.totalPrice > 499 ? <td><b>{props.totalPrice}</b></td> : <td><b>{props.totalPrice+18}</b></td>}
      </tr> 
      <tr style={{textAlign:"end"}}>
          <td colSpan="2" style={{color:"#00a100"}}><b>you save {props.totalMrp-props.totalPrice}</b></td> 
        </tr> 
  </tbody>
</Table>
   )

 }

export default PriceDetails