import axiosIntance from "../helper/axios";
import { categoryCostants } from "./constants";

export const getAllCategory = () => {
    return async dispatch => {

        dispatch({ type: categoryCostants.GET_ALL_CATEGORIES_REQUEST });
        const res = await axiosIntance.get('category/getcategory');
        // console.log(res);
        if(res.status === 200){

            const { categoryList } = res.data;

            dispatch({
                type: categoryCostants.GET_ALL_CATEGORIES_SUCCESS,
                payload: { categories: categoryList }
            });
        }else{
            dispatch({
                type: categoryCostants.GET_ALL_CATEGORIES_fAILURE,
                payload: { error: res.data.error }
            })
        }
    } 
}

