import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination, Navigation } from "swiper";
import "swiper/swiper-bundle.css";
import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { getAllCategory } from "../../actions/category.action";
import Layout from "../Layout";
import { Col, Container, Image, Row } from "react-bootstrap";
import { genratePublicUrl } from "../../urlConfig";
import { Link } from "react-router-dom";

// Import Swiper styles
//import 'swiper/swiper.scss';
SwiperCore.use([Pagination, Navigation]);

const SwiperPage = (props) => {
  const category = useSelector((state) => state.category);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getAllCategory());
  }, []);

  const renderCategories = (categories) => {
    if (categories) {
        return categories.map((category) => {
          console.log(category);
                return (
                  
                  <Col xs={12} sm={6} md={4} lg={3}>
                    <a href={`/product/${category.slug}`}>
                      <Image src={`./images/${category.name}.png`} /*src={genratePublicUrl(category.categoryImage)}*/ thumbnail/>
                    </a>
                  </Col>
                  
                    
                )
            }
        )
    }
}

  // const renderCategories = (categories) => {
  //   const mycategories = [];
  //   for (let category of categories) {
  //     mycategories.push(
  //       <Link to={`/:${category.slug}`}>
  //         <option value={category.slug}>{category.name}</option>
  //       </Link>
  //     );
  //   }
  //   return mycategories;
  // };

  return (
        <Container>
          <div style={{marginTop:"65px"}}>
            <h4>SHOP BY CATEGORIES</h4>
            {
              category.loading && <div>loading</div>
            }
          <Row>{category.categories.length > 0
        ? renderCategories(category.categories)
        : null}</Row>
          
        </div> 
        </Container>
               
  );
};

export default SwiperPage;
