import React, { useEffect } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { getOrder } from "../../actions";
import Layout from "../../component/Layout";
import Card from "../../component/UI/Card";
import Price from "../../component/UI/Price";
import { genratePublicUrl } from "../../urlConfig";

import "./style.css";

/**
 * @author
 * @function OrderDetails
 **/

const OrderDetailsPage = (props) => {
  const dispatch = useDispatch();
  const orderDetails = useSelector((state) => state.user.orderDetails);

  useEffect(() => {
    // console.log({ props });
    const payload = {
      orderId: props.match.params.orderId,
    };
    // console.log(payload)
    dispatch(getOrder(payload));
  }, []);

  const formatDate = (date) => {
    if (date) {
      const d = new Date(date);
      return `${d.getDate()}-${d.getMonth() + 1}-${d.getFullYear()}`;
    }
    return "";
  };

  const formatDate2 = (date) => {
    const month = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "June",
      "July",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ];
    if (date) {
      const d = new Date(date);
      return `${month[d.getMonth()]} ${d.getDate()}, ${d.getFullYear()}`;
    }
  };

  if (!(orderDetails && orderDetails.address)) {
    return null;
  }

  return (
    <Layout>
      <Container>
        <Card
          style={{
            margin: "10px 0",
          }}
        >
          <div className="delAdrContainer">
            <Row>
              <Col xs={12} sm={12} md={6} lg={6}>
            <div className="delAdrDetails">
              <div className="delTitle">Delivery Address</div>
              <div className="delName">{orderDetails.address.name} {orderDetails.address.addressType}</div>
              <div className="delAddress">{orderDetails.address.locality}, {orderDetails.address.address}</div>
              <div className="delAddress"></div>
              <div className="delPhoneNumber">
                {orderDetails.address.mobileNumber}
              </div>
            </div>
            </Col>
            <Col xs={12} sm={12} md={6} lg={6}>
            <div className="delMoreActionContainer">
            <div style={{ padding: "25px 30px" }}>
              <div className="orderTrack">
                {orderDetails.orderStatus.map((status) => (
                  <div
                    className={`orderStatus ${
                      status.isCompleted ? "active" : ""
                    }`}
                  >
                    <div
                      className={`point ${status.isCompleted ? "active" : ""}`}
                    ></div>
                    <div className="orderInfo">
                      <div className="status">{status.type}</div>
                      <div className="date">{formatDate(status.date)}</div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
            <div style={{ marginTop: "10px", fontWeight: "500", fontSize: 14 }}>
              {orderDetails.orderStatus[3].isCompleted &&
                `Delivered on ${formatDate2(orderDetails.orderStatus[3].date)}`}
            </div>
            </div>
            </Col>
            </Row>
          </div>
        </Card>
        
        <Row>
        {orderDetails.items.map((item, index) => (
          <Col xs={12} sm={12} md={6} lg={4}>
          <Card
            style={{ display: "flex", padding: "20px 0", margin: "10px 0" }}
          >
            <Row>
              <Col xs={4} sm={4} md={4} lg={4} className="delItemImgContainer">
                <img src={genratePublicUrl(item.productId.productPictures[0].img)} alt={item.productId.name} />
              </Col>
              <Col xs={8} sm={8} md={8} lg={8}>
                <div className="delItemName">{item.productId.name}</div>
                <Price value={item.payablePrice} />
                <div>{item.purchasedQty}</div>
              </Col>
            </Row>
            
            
          </Card></Col>
        ))}
        </Row>
      </Container>
    </Layout>
  );
};

export default OrderDetailsPage;
