import './App.css';
import HomePage from './container/HomePage';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import SwiperPage from './component/MenuHeader';
import ProductPage from './component/ProductPage';
import { useDispatch, useSelector } from 'react-redux';
import React, { useEffect } from 'react';
import { isUserLoggedIn } from './actions';
import CartPage from './container/CartPage';
import { updateCart } from './actions/cart.action';
import CheckoutPage from './container/CheckoutPage';
import OrderPage from './container/OrderPage';
import OrderDetailsPage from './container/OrderDetailPage';



function App() {

  const dispatch = useDispatch();
  const auth = useSelector(state => state.auth);

  useEffect(() => {
    if(!auth.authenticate){
      dispatch(isUserLoggedIn());
    }
  }, [auth.authenticate]);

  useEffect(() => {
    // console.log('App.js - updateCart')
    dispatch(updateCart());
  }, [auth.authenticate]);

  return (
    <div>
      <Router>
        <Switch>
          <Route path='/cart' component={CartPage} />
          <Route path='/order_details/:orderId' component={OrderDetailsPage} />
          <Route path='/account/orders' component={OrderPage} />
          
          <Route path='/product/:slug' component={ProductPage} />
          <Route path='/checkout' component={CheckoutPage} />
          <Route path='/category' component={SwiperPage} />
          <Route exact path='/' component={HomePage} />
          
        </Switch>
      </Router>
    </div>
    
  );
}

export default App;
