import React, { useEffect, useState } from "react";
import { Button, Col, Container, Image, Row } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { getProducts, getProductsBySlug } from "../../actions";
import { addToCart } from "../../actions/cart.action";
import { genratePublicUrl } from "../../urlConfig";
import DropDown from "../Dropdown";
import Layout from "../Layout";
import './style.css'

/**
 * @author
 * @function ProductPage
 **/

const ProductPage = (props) => {
  const product = useSelector((state) => state.product);
  const cart = useSelector((state) => state.cart);
  const dispatch = useDispatch();

  useEffect(() => {
    // console.log(props)
    const { match } = props;  
    dispatch(getProductsBySlug(match.params.slug));
  }, []);

  const renderProducts = (products) => {
    
    
    const myproducts = [];
    for (let product of products) {
      const item = Object.keys(cart.cartItems).filter(key => cart.cartItems[key]._id==product._id);
      // console.log(item);
      myproducts.push(
        <Col key={product._id} md={6} sm={12} lg={4}>
          <div className="text-left product-box">
          <Row>
            <Col xs={5} md={4} sm={4} lg={4}><Image className="img-thumbnail" src={genratePublicUrl(product.productPictures[0].img)} alt={`${product.name} image`} thumbnail/></Col>
            <Col xs={7} md={8} sm={8} lg={8}><div className="product__name">{product.name}</div>
            <div  style={{lineHeight:"40px"}}>
            <span className="product__name"> {product.price}</span>
            {product.quantity > product.price ? <> <span className="product__info"> <del>{product.quantity}</del> </span>
            <span className="product__info2">
              ({Math.round((product.quantity-product.price)/product.quantity*100)}% OFF)
            </span>
            </> : null}
            </div>
            <div> 
            {
              item.length > 0 ? <button style={{backgroundColor:"#fb641b", border:"none",color:"#fff"}}>Added To Cart</button> : <button
              style={{backgroundColor:"#00a100", border:"none",color:"#fff"}}
              onClick={() => {
                const { _id, name, price, quantity } = product;
                const img = product.productPictures[0].img;
                dispatch(addToCart({ _id, name, price, quantity, img }));
              }}
            >
              Add to Cart
            </button>
            }
            </div>
            </Col>
          </Row>
          </div>
          
            

            
        </Col>
      );
    }
    return myproducts;
  };

  return (
    <Layout>
      <DropDown/>
      <Container fluid style={{backgroundColor:"#fff"}}>
        <h3>{props.match.params.slug}</h3>
      <Row>
      {/* <Col className="product-box" key={product._id} xs={6} md={4} sm={6} lg={3}>
          
          <Image className="img-thumbnail" src="https://5.imimg.com/data5/DS/WE/GLADMIN-17396827/aashirvaad-atta-5-kg-500x500.png" thumbnail/>

          <div>Aashirvaad Ata</div>
          <div>200</div>
          <div>n/a</div>
          <div>Aashirvaad Ata</div>
          <Button>Add to Cart</Button>

      </Col> */}
      {product.loading && <div>loding...</div>}
        { product.products ? renderProducts(product.products) : <h1>No Products for this Category</h1>}
        </Row>
    </Container>
    </Layout>
    
  );
};

export default ProductPage;
