import React, { useState, useEffect } from "react";
import { Link, NavLink } from "react-router-dom";
import { Navbar, Nav, Container, Button, Collapse } from "react-bootstrap";
import Layout from "../Layout";
import { IoIosCart } from "react-icons/io";
import MyVerticallyCenteredModal from "../UI/Modal";
import {MaterialButton, MaterialInput} from "../UI/MaterialUI";
import { useDispatch, useSelector } from "react-redux";
import { login, signout, signup as _signup } from "../../actions";
import Cart from '../UI/Cart';
//import './style.css';

/**
 * @author
 * @function Header
 **/

const Headers = (props) => {
  // const [loginModal, setLoginModal] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [modalShow, setModalShow] = useState(false);
  const [signup, setSignup] = useState(false);
  const [accountModal, setAccountModal] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [error, setError] = useState(null);
  const auth = useSelector((state) => state.auth);
  const cart = useSelector((state) => state.cart);
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [chk, setCheck] = useState('');

  const userSignup = () => {
    const user = { firstName, lastName, email, password };
    // console.log(user);
     dispatch(_signup(user));
  }

  
  const userLogin = () => {
    if (signup) {
      userSignup();
    } else {
      dispatch(login({ email, password }));
    }
  };

  const logout = () => {
    setAccountModal(false)
    dispatch(signout());
  }

  useEffect(() => {
    if (auth.authenticate) {
      setModalShow(false);
    }
  }, [auth.authenticate]);

  return (
    <Navbar
      collapseOnSelect
      fixed="top"
      expand="lg"
      variant="dark"
      style={{
        zIndex: 1,
        display: "flex",
        backgroundColor: "#2874f0",
      }}
    >
      <Container fluid>

      <MyVerticallyCenteredModal
          show={accountModal}
          onHide={() => setAccountModal(false)}
          modalTitle="ACCOUNT DETAILS"
        >
          <div style={{lineHeight:"20px"}}>
            <h5>Name :{auth.user.fullName}</h5>
            <h5>Email :{auth.user.email}</h5>
            <a onClick={logout}>Logout</a>
          </div>
          
        </MyVerticallyCenteredModal>

        {/* signup modal */}
        {/* <MyVerticallyCenteredModal
          show={signModalShow}
          onHide={() => setSignModalShow(false)}
          modalTitle={"Login"}
        >
          <MaterialInput
            type="email"
            label="Enter Email/Enter Mobile Number"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />

          <MaterialInput
            type="password"
            label="Enter Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <div className="text-center">
            <Button>sign In</Button>
          </div>
        </MyVerticallyCenteredModal> */}
        
        {/* login modal */}
        <MyVerticallyCenteredModal
          show={modalShow}
          onHide={() => setModalShow(false)}
          modalTitle={signup ? "Register" : "Login"}
        >
          <div style={{padding:"0px 5px"}}>
          <div>
            {setCheck}
          </div>

          {signup && (
            <MaterialInput
            type="text"
            label="First Name"
            error=""
            value={firstName}
            onChange={(e) => setFirstName(e.target.value)}
          />
          )}

          {signup && (
            <MaterialInput
            type="text"
            label="Last Name"
            error=""
            value={lastName}
            onChange={(e) => setLastName(e.target.value)}
          />
          )}
          
          <MaterialInput
            type="email"
            label="Email Address"
            value={email}
            error=""
            onChange={(e) => setEmail(e.target.value)}
          />
          
          <MaterialInput
            type="password"
            label="Password"
            error=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <div style={{marginTop:"20px"}}>
          {auth.error && (
                  <div style={{ color: "red", fontSize: 12 }}>{auth.error}</div>
                )}
                {
                  auth.loading && <div>loading....</div>
                }
          
          <MaterialButton
            title={signup ? "REGISTER" : "LOGIN"}
            onClick={userLogin}
          />
          </div>
        
          {signup ? null : (<div className="text-center">

            <p>New Customer</p>
        
            <button onClick={() => setSignup(true)}>
              create new account
            </button>
          </div>)}
          </div>
        </MyVerticallyCenteredModal>

        <Nav style={{ marginRight: "1em", fontSize: "25px" }}>
          <NavLink className="nav-link" to="/cart">
          <IoIosCart />{Object.keys(cart.cartItems).length > 0 ?<span style={{
            backgroundColor:"#dd0608",
            fontSize:"10px",
            position:"absolute",
            padding:"3px 6px",
            borderRadius:"50%",
            color:"#fff"

          }}>{Object.keys(cart.cartItems).length}</span> : null}
          </NavLink>
        </Nav>
        <NavLink to="/">
          <Navbar.Brand style={{ alignItems: "center", fontSize: "25px" }}>
            <i>
              <b>ki2</b>mart<span style={{fontSize:"14px"}}>.com</span>
            </i>
          </Navbar.Brand>
        </NavLink>

        <Navbar.Toggle
          aria-controls="responsive-navbar-nav"
          className="navbar-left"
        />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            {/*
                <NavDropdown title="Dropdown" id="collasible-nav-dropdown">
                    <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                </NavDropdown>*/}
          </Nav>
          <Nav>
            {auth.authenticate ? (
              <Nav 
                className="nav-link"
                
              >
                <i
                  onClick={() => setAccountModal(true)}
                  // aria-controls="example-fade-text"
                  // aria-expanded={open} style={{ color: "white" }}
                >
                  Hello {auth.user.firstName}
                </i>
                {/* <Collapse in={open}>
                  <a onClick={(e) => {
                    e.preventDefault();
                    logout();
                  }}>Logout</a>
                </Collapse> */}
              </Nav>
              
            ) : (
              <Nav className="nav-link" style={{cursor:"pointer"}} onClick={() => {setModalShow(true); setSignup(false)}}>
                Sign in
              </Nav>
            )}
            <NavLink to="/category" className="nav-link">
              Category
            </NavLink>
            <NavLink to="/account/orders" className="nav-link">
              Orders
            </NavLink>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Headers;
