import React, { useEffect, useState } from "react";
import { Container } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { addAddress } from "../../actions";
import {MaterialButton, MaterialInput} from "../../component/UI/MaterialUI";
// import MaterialButton from "../../component/UI/MaterialButton";
// import MaterialInput from "../../component/UI/MaterialInput";
import './style.css'

/**
 * @author
 * @function AddressForm
 **/

const AddressForm = (props) => {
  const { initialData } = props;
  const [name, setName] = useState(initialData ? initialData.name : "");
  const [mobileNumber, setMobileNumber] = useState(
    initialData ? initialData.mobileNumber : ""
  );
  const [pinCode, setPinCode] = useState(
    initialData ? initialData.pinCode : 473331
  );
  const [locality, setLocality] = useState(
    initialData ? initialData.locality : ""
  );
  const [address, setAddress] = useState(
    initialData ? initialData.address : ""
  );
  const [cityDistrictTown, setCityDistrictTown] = useState(
    initialData ? initialData.cityDistrictTown : "Ashoknagar"
  );
  const [state, setState] = useState(initialData ? initialData.state : "Madhya Pradesh");
  const [landmark, setLandmark] = useState(
    initialData ? initialData.landmark : ""
  );
  const [alternatePhone, setAlternatePhone] = useState(
    initialData ? initialData.alternatePhone : ""
  );
  const [addressType, setAddressType] = useState(
    initialData ? initialData.addressType : ""
  );
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  const [submitFlag, setSubmitFlag] = useState(false);
  const [id, setId] = useState(initialData ? initialData._id : "");

  const inputContainer = {
    width: "fit-content",
    marginRight: 10,
  };

  const onAddressSubmit = (e) => {
    const payload = {
      address: {
        name,
        mobileNumber,
        pinCode,
        locality,
        address,
        cityDistrictTown,
        state,
        landmark,
        alternatePhone,
        addressType,
      },
    };
    // console.log(payload);
    if (id) {
      payload.address._id = id;
    }
    dispatch(addAddress(payload));
    setSubmitFlag(true);
  };

  useEffect(() => {
    // console.log("addressCount", user.address);
    if (submitFlag) {
      // console.log("where are we", user);
      let _address = {};
      if (id) {
        _address = {
          _id: id,
          name,
          mobileNumber,
          pinCode,
          locality,
          address,
          cityDistrictTown,
          state,
          landmark,
          alternatePhone,
          addressType,
        };
      } else {
        _address = user.address.slice(user.address.length - 1)[0];
      }

      props.onSubmitForm(_address);
    }
  }, [user.address]);

  const renderAddressForm = () => {
    return (
      <>
          <div style={inputContainer}>
            <MaterialInput
              label="Full Name*"
              value={name}
              error="Required"
              onChange={(e) => setName(e.target.value)}
            />
          </div>
          <div style={inputContainer}>
            <MaterialInput
              label="Phone number*"
              value={mobileNumber}
              error="Required"
              onChange={(e) => setMobileNumber(e.target.value)}
            />
          </div>
        <div className="">
          <div style={inputContainer}>

            <MaterialInput
              label="Pincode"
              readOnly="true"
              error=""
              value={pinCode}
            />
          </div>
          <div style={inputContainer}>
            <MaterialInput
              label="House No., BuildingName*"
              value={locality}
              error="Required"
              onChange={(e) => setLocality(e.target.value)}
            />
          </div>
        </div>
        <div className="">
          <div style={inputContainer}>
            <MaterialInput
              label="Road name, Area, Colony*"
              value={address}
              error="Required"
              onChange={(e) => setAddress(e.target.value)}
            />
          </div>
        </div>

        <div className="">
          <div style={inputContainer}>
            <MaterialInput
              label="City*"
              readOnly="true"
              error=""
              value={cityDistrictTown}
            />
          </div>
          <div style={inputContainer}>
            <MaterialInput
              label="State"
              readOnly="true"
              error=""
              value={state}
            />
          </div>
        </div>
        <div className="">
          <div style={inputContainer}>
            <MaterialInput
              label="Landmark"
              value={landmark}
              error=""
              onChange={(e) => setLandmark(e.target.value)}
            />
          </div>
          <div style={inputContainer}>
            <MaterialInput
              label="Alternate Phone"
              value={alternatePhone}
              error=""
              onChange={(e) => setAlternatePhone(e.target.value)}
            />
          </div>
        </div>
        <div>
          <label style={{marginTop:"20px"}}>Address Type*</label>
          <div className="flexRow">
            <div>
              <input
                type="radio"
                onClick={() => setAddressType("home")}
                name="addressType"
                value="home"
              />
              <span>Home</span>
            </div>
            <div>
              <input
                type="radio"
                onClick={() => setAddressType("work")}
                name="addressType"
                value="work"
              />
              <span>Work</span>
            </div>
          </div>
        </div>
        <div className="flexRow">
          {
            (
              name ==='' ||
              mobileNumber.length < 10 ||
              locality === '' ||
              address === '' ||
              addressType === ''

            ) ? <MaterialButton
            title="SAVE AND DELIVER HERE"
            bgColor="gray"
            style={{
              width: "250px",
              margin: "20px 0",
            }}
          /> : <MaterialButton
            title="SAVE AND DELIVER HERE"
            onClick={onAddressSubmit}
            style={{
              width: "250px",
              margin: "20px 0",
            }}
          />
          }
        
        </div>
      </>
    );
  };

  if (props.withoutLayout) {
    return <div>{renderAddressForm()}</div>;
  }

  return (

    // <Container>
    <div className="checkoutStep" style={{ background: "#f5faff" }}>
      <div className={`checkoutHeader`}>
        <div>
          <span className="stepNumber">+</span>
          <span className="stepTitle">{"ADD NEW ADDRESS"}</span>
        </div>
      </div>
      <div
        style={{
          padding: "0 60px",
          paddingBottom: "20px",
          boxSizing: "border-box",
        }}
      >
        {renderAddressForm()}
      </div>
    </div>
    // </Container>
  );
};

export default AddressForm;
