import React from "react";
import { useState } from "react";
import { Col, Image, Row } from "react-bootstrap";
import {
  IoIosAddCircleOutline,
  IoIosRemoveCircleOutline,
} from "react-icons/io";
import { useSelector } from "react-redux";
import { genratePublicUrl } from "../../../urlConfig";
import './style.css'

/**
 * @author
 * @function CartItem
 **/

const CartItem = (props) => {
  const [qty, setQty] = useState(props.cartItem.qty);

  const { _id, name, price, quantity, img } = props.cartItem;

  const auth= useSelector(state => state.auth);

  const onQuantityIncrement = () => {
    setQty(qty + 1);
    props.onQuantityInc(_id, qty + 1);
  };

  const onQuantityDecrement = () => {
    if (qty <= 1) return;
    setQty(qty - 1);
    props.onQuantityDec(_id, qty - 1);
  };

  return (
    <Row style={{padding:"4px 0px"}}>
      <Col xs={5} md={4} sm={4} lg={2} className="text-center" style={{borderBottom:"1px solid rgba(0,0,0,.1)", padding:"4px 0px"}}>
        <Image
          className="img-thumbnail"
          src={genratePublicUrl(img)}
          thumbnail
        />
         {auth.authenticate ? (<div
            onClick={() => props.onRemoveCartItem(_id)}
            style={{fontSize:"12px"}}
          >
            Remove
          </div>) : null}
      </Col>
      <Col xs={7} md={8} sm={8} lg={10} style={{borderBottom:"1px solid rgba(0,0,0,.1)", padding:"4px 0px"}}>
        <div style={{lineHeight:"25px"}} className="product__name">{name}</div>
        <div style={{lineHeight:"25px"}}>
        <span className="product__name"> {price * qty}</span>
        <span className="product__info">
          {" "}
          (
          <del>
            <i> {quantity * qty}</i>
          </del>
          ){" "}
        </span>
        {/* <span className="product__info1">(save {quantity-price})</span> */}
        <span className="product__info1">({Math.round((quantity-price)/quantity*100)}% OFF)</span></div>
        <div className="text-right">
          <IoIosRemoveCircleOutline style={{fontSize:"24px",cursor:"pointer", color:"darkred"}} onClick={onQuantityDecrement} />{" "}
          <span>{qty} {" "}</span>
          <IoIosAddCircleOutline style={{fontSize:"24px",cursor:"pointer", color:"darkgreen"}} onClick={onQuantityIncrement} />
        </div>
        
        
      </Col>
      
    </Row>
  );
};

export default CartItem;
