import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Button, Col, Container, Image, Row, Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { login, signup as _signup } from "../../actions";
import { addToCart, getCartItems, removeCartItem } from "../../actions/cart.action";
import Layout from "../../component/Layout";
import CartItem from "./CartItem";
import MyVerticallyCenteredModal from '../../component/UI/Modal';
import PriceDetails from "../../component/PriceDetails";
import {MaterialButton, MaterialInput} from "../../component/UI/MaterialUI";
import './style.css';

/**
 * @author
 * @function CartPage
 **/

const CartPage = (props) => {

    //login
    const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [modalShow, setModalShow] = useState(false);
  const [signup, setSignup] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [error, setError] = useState(null);
  const auth = useSelector((state) => state.auth);
  //const dispatch = useDispatch();
  //const [open, setOpen] = useState(false);

  const userSignup = () => {
    const user = { firstName, lastName, email, password };
    // console.log(user);
     dispatch(_signup(user));
  }

  
  const userLogin = () => {
    if (signup) {
      userSignup();
    } else {
      dispatch(login({ email, password }));
    }
  };

  useEffect(() => {
    if (auth.authenticate) {
      setModalShow(false);
    }
  }, [auth.authenticate]);




    const cart = useSelector(state => state.cart);
    //const auth = useSelector(state => state.auth);
    // const cartItems = cart.cartItems;
    const [cartItems, setCartItems] = useState(cart.cartItems);
    const dispatch = useDispatch();

    useEffect(() => {
      setCartItems(cart.cartItems)
    }, [cart.cartItems]);

    useEffect(() => {
      if(auth.authenticate){
        dispatch(getCartItems());
      }
    }, [auth.authenticate]);

    const onQuantityIncrement = (_id, qty) => {
      const { name, price, quantity, img } = cartItems[_id];
      dispatch(addToCart({ _id, name, price, quantity, img }, 1));
    };

    const onQuantityDecrement = (_id, qty) => {
      const { name, price, quantity, img } = cartItems[_id];
      dispatch(addToCart({ _id, name, price, quantity, img }, -1));
    };

    const onRemoveCartItem = (_id) => {
      dispatch(removeCartItem({ productId: _id }));
    };


    if (props.onlyCartItems) {
      return (
        <>
          {Object.keys(cartItems).map((key, index) => (
            <CartItem
              key={index}
              cartItem={cartItems[key]}
              onQuantityInc={onQuantityIncrement}
              onQuantityDec={onQuantityDecrement}
              onRemoveCartItem={onRemoveCartItem}
            />
          ))}
        </>
      );
    }
  


        



    const onCheckout = () => {
      if(auth.authenticate){
        props.history.push('/checkout')
      } else{
        setModalShow(true);
        setSignup(false);
      }
    }


  return (
    <Layout>
      <Container>
        {/* login modal */}
        <MyVerticallyCenteredModal
          show={modalShow}
          onHide={() => setModalShow(false)}
          modalTitle={signup ? "Register" : "Login"}
        >
          <div style={{padding:"0px 5px"}}>

          {signup && (
            <MaterialInput
            type="text"
            label="First Name"
            error=""
            value={firstName}
            onChange={(e) => setFirstName(e.target.value)}
          />
          )}

          {signup && (
            <MaterialInput
            type="text"
            label="Last Name"
            error=""
            value={lastName}
            onChange={(e) => setLastName(e.target.value)}
          />
          )}
          
          <MaterialInput
            type="email"
            label="Email Address"
            error=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          
          <MaterialInput
            type="password"
            label="Password"
            error=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <div style={{marginTop:"20px", marginBottom:"20px"}}>
          {auth.error && (
                  <div style={{ color: "red", fontSize: 12 }}>{auth.error}</div>
                )}
          
          <MaterialButton
            title={signup ? "REGISTER" : "LOGIN"}
            onClick={userLogin}
          />
          </div>
        
          {signup ? null : (<div className="text-center">

            <div>New Customer</div>
        
            <button onClick={() => setSignup(true)}>
              create new account
            </button>
          </div>)}
          </div>
        </MyVerticallyCenteredModal>





        <div style={{ marginTop: "70px" }}></div>
        {
          Object.keys(cartItems).length > 0 ? <Row>
          <Col xs={12} sm={12} md={8} lg={8} style={{ marginTop: "5px" }}>
            <div className="grocery__basket">
            <h5>Groceries Basket</h5>
            <hr/>
            {
                Object.keys(cartItems).map((key, index) => 
                <CartItem
                  key={index}
                  cartItem={cartItems[key]}
                  onQuantityInc={onQuantityIncrement}
                  onQuantityDec={onQuantityDecrement}
                  onRemoveCartItem={onRemoveCartItem}
                />
                ) 
            }
            </div>
          </Col>
          <Col xs={12} sm={12} md={4} lg={4} style={{ marginTop: "5px" }}>
            <div className="grocery__basket">
          <PriceDetails
            totalItem={Object.keys(cartItems).reduce(function (qty, key) {
              return qty + cartItems[key].qty;
            }, 0)}
            totalPrice={Object.keys(cartItems).reduce((totalPrice, key) => {
              const { price, qty } = cartItems[key];
              return totalPrice + price * qty;
            }, 0)}
            totalMrp={Object.keys(cartItems).reduce((totalMrp, key) => {
              const { quantity, qty } = cartItems[key];
              return totalMrp + quantity * qty;
            }, 0)}
          /></div>
          <div style={{marginTop:"5px"}}>
          
              <MaterialButton
                title="PLACE ORDER"
                onClick={() => onCheckout()}
              />
              </div>
          </Col>
        </Row> : 
          <h3>No Item Added To Cart</h3>
        }
        
      </Container>
    </Layout>
  );
};

export default CartPage;
