import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { getOrders } from "../../actions";
import Layout from "../../component/Layout";
import Card from "../../component/UI/Card";
import { IoIosArrowForward } from "react-icons/io";

import "./style.css";
import { Breed } from "../../component/UI/MaterialUI";
import { Col, Container, Row } from "react-bootstrap";
import { genratePublicUrl } from "../../urlConfig";

/**
 * @author
 * @function OrderPage
 **/

const OrderPage = (props) => {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  const formatDate = (date) => {
    if (date) {
      const d = new Date(date);
      return `${d.getDate()}-${d.getMonth() + 1}-${d.getFullYear()}`;
    }
    return "";
  };

  useEffect(() => {
    dispatch(getOrders());
  }, []);

  // console.log(user);

  return (
    <Layout>
        <Container>
      <div style={{ maxWidth: "1160px", marginTop: "75px" }}>
        <Breed
          breed={[
            { name: "Home", href: "/" },
            { name: "My Account", href: "/account" },
            { name: "My Orders", href: "/account/orders" },
          ]}
          breedIcon={<IoIosArrowForward />}
        />
        {
          user.orders.map((order) => {
            return  (<Card style={{ margin: "5px 0", padding:"5px 10px"}}>
              <Link
                to={`/order_details/${order._id}`}
                // className="orderItemContainer"
              >
            
                <Row>
                  
                  <Col xs={12} sm={6} md={6} lg={3}>{`Ordered on ${formatDate(order.orderStatus[0].date)}`}</Col>
                  <Col xs={12} sm={6} md={6} lg={3}> {order.totalAmount}({order.items.length} items)--</Col>
                  {/* <div>{order.items.length}--</div> */}
                  <Col xs={12} sm={6} md={6} lg={3}>CASH ON DELIVERY</Col>
                  <Col xs={12} sm={6} md={6} lg={3}>{order.orderStatus[3].isCompleted &&
                `Delivered on ${formatDate(order.orderStatus[3].date)}`}</Col>

                </Row>
              </Link>
            </Card>)

          })
        }
        {/* {user.orders.map((order) => {
          return order.items.map((item) => (
            <Card style={{ display: "block", margin: "5px 0" }}>
              <Link
                to={`/order_details/${order._id}`}
                className="orderItemContainer"
              >
                <div className="orderImgContainer">
                  <img
                    className="orderImg"
                    src={genratePublicUrl(item.productId.productPictures[0].img)}
                  />
                </div>
                <div className="orderRow">
                  <div className="orderName">{item.productId.name}</div>
                  <div className="orderPrice">
                    <BiRupee />
                    {item.payablePrice}
                  </div>
                  <div>{order.paymentStatus}</div>
                </div>
              </Link>
            </Card>
          ));
        })} */}
      </div>
      </Container>
    </Layout>
  );
};

export default OrderPage;
